import React from 'react'
import './styleNav.css'

import { Link, NavLink } from 'react-router-dom'


export const NavBar = () => {
  return (
    <nav>
        <div className='li'>
            <Link 
                className='a'
                to='/'
            >
                Inicio
            </Link>
        </div>
         <div className='li'>
            <NavLink 
                className='a'
                to='/socios'
            >
                Socios
            </NavLink>
        </div>
         <div className='li'   >
            <NavLink  
                className='a'
                to='/facturas'
            >
                Facturas
            </NavLink>  
        </div>
        
    </nav>
  )
}
