import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import { useDispatch, useSelector } from 'react-redux';
import { closeModalActPre, noUpdatedModalActPres, updatedModalActPre } from '../../actions/modalActPre';
import { actualizarSocio } from '../../actions/socio';


export const EditSocio = () => {

    const dispatch = useDispatch()


    const {isOpen, socio, actualizado} = useSelector(state => state.modal)
  
    const [socioValues, setSocioValues] = useState({
        cedula:'',
        nombre: '',
        apellidos: '',
        celular: '',
        placa: '',
    })

    const {cedula, nombre, apellidos, celular, placa} = socioValues;

    const handleInputChange = ({target }) => {

        setSocioValues({
            ...socioValues,
            [target.name]: target.value
        });
    }


    const customStyles = {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    }

    Modal.setAppElement('#root');

    useEffect(() => {
        
        if(socio){
            setSocioValues(socio); 
            
        }
        if(actualizado){

            dispatch(noUpdatedModalActPres())
            
        }

    }, [socio, dispatch, actualizado])

 
    const handleSumit = (e) => {
        e.preventDefault();
        
        dispatch(actualizarSocio(socioValues));

        dispatch( updatedModalActPre() ) 
       closeModal()   
    }


    const closeModal = () => {
        dispatch(closeModalActPre())

    }

 
  return (

    <Modal
    
    isOpen={isOpen}
    //onAfterOpen={afterOpenModal}
    onRequestClose={closeModal}
    style={customStyles}
    closeTimeoutMS={200}

    className='modal'
    overlayClassName='modal-fondo'

  >
        <h2>Editor de actividad</h2>
        <form onSubmit={handleSumit} className='formularioCliente'>
            <div className='row'>
            <div>
                    <input
                    type='number'
                    name='cedula'
                    className='form-control'
                    placeholder='Cedula'
                    autoComplete='off'
                    value={cedula}
                    onChange={handleInputChange}
                    />
                </div>

                <div className='mt-2'>
                    <input
                    type='text'
                    name='nombre'
                    className='form-control'
                    placeholder='Nombre'
                    autoComplete='off'
                    value={nombre} 
                    onChange={handleInputChange} 
                    />
                </div>

                <div className='mt-2'>
                    <input
                    type='text'
                    name='apellidos'
                    className='form-control'
                    placeholder='Apellidos'
                    autoComplete='off'
                    value={apellidos} 
                    onChange={handleInputChange} 
                    />
                </div>

                <div className='mt-2'>
                    <input
                    type='number'
                    name='celular'
                    className='form-control'
                    placeholder='Direccion'
                    autoComplete='off'
                     value={celular} 
                     onChange={handleInputChange} 
                    />
                </div>

                <div className='mt-2'>
                    <input
                    type='text'
                    name='placa'
                    className='form-control'
                    placeholder='Placa'
                    autoComplete='off'
                    value={placa} 
                    onChange={handleInputChange} 
                    />
                </div>
                <div className='row mt-2'>
                    <button type='submit' className='btn btn-primary'>
                        Actualizar
                    </button>
                </div>
                
            </div>
        </form>
        <div className='row mt-2'>
            <button className='btn btn-danger' onClick={closeModal}>
                Cancelar
            </button>
        </div>



  </Modal>
  )
}
