import { Routes, Route, BrowserRouter } from "react-router-dom";



import { FacturaScreen } from "../Components/Facturas/FacturaScreen";
import { HomeScreen } from "../Components/Home/HomeScreen";
import { SocioScreen } from "../Components/Socios/SocioScreen";
import { Carga } from "../helpers/Carga"
import { NavBar } from "../UI/NavBar";


export const AppRouter = () => {

    
    return (
        <BrowserRouter >
            <NavBar /> 
            <Routes>
                <Route path="/" element={<HomeScreen />} />
                <Route path="/socios" element={<SocioScreen />} />
                <Route path="/facturas" element={<FacturaScreen />} />
                
            </Routes>


            <Carga /> 
        </BrowserRouter>
    )
};
